import React, {
  useState, useEffect, useRef, useLayoutEffect,
} from 'react';
import { Box, Flex } from 'rebass';
import PropTypes from 'prop-types';
import CarouselItem from './CarouselItem';
import CarouselDot from './CarouselDot';

const CAROUSEL_DURATION = 6000;

const Carousel = ({
  slides, activeIndex, setActiveIndex, carouselWidth, setCarouselWidth, advanceSlider,
}) => {
  const [isActive, setIsActive] = useState(true);
  const carouselRef = useRef();

  useEffect(() => {
    // Store the interval timer so it can be cancelled later
    let timer = null;

    // Start / stop the carousel
    if (isActive) {
      timer = setInterval(advanceSlider, CAROUSEL_DURATION);
    } else {
      clearInterval(timer);
    }

    // Clear the interval on unmount
    return () => clearInterval(timer);
  }, [isActive, advanceSlider]);

  useLayoutEffect(() => {
    const handleResize = () => {
      setCarouselWidth(carouselRef.current.offsetWidth);
    };

    window.addEventListener('resize', handleResize);
    handleResize();

    return () => window.removeEventListener('resize', handleResize);
  }, [setCarouselWidth]);

  // eslint-disable-next-line
  const transform = 'translate3d(-' + (carouselWidth * activeIndex) + 'px, 0, 0)';

  return (
    <Box
      data-testid="carousel"
      variant="carousel"
      onMouseEnter={() => setIsActive(false)}
      onMouseLeave={() => setIsActive(true)}
      onClick={advanceSlider}
      ref={carouselRef}
      sx={{
        height: '100vh',
        gridArea: 'carousel',
        msGridColumn: '1',
        width: '100%',
        position: 'relative',
        overflow: 'hidden',
        display: ['none', 'none', 'block'],
        cursor: 'pointer',
      }}
    >
      <Flex sx={{
        transition: 'transform 0.5s',
        transform,
        position: 'absolute',
        top: 0,
        bottom: 0,
        left: 0,
        width: carouselWidth * slides.length,
      }}
      >
        {slides.map((item) => (
          <CarouselItem
            key={item.id}
            item={item}
            carouselWidth={carouselWidth}
          />
        ))}
      </Flex>
      <Flex sx={{
        position: 'absolute', left: 32, bottom: 32, zIndex: 1,
      }}
      >
        {slides.map((item, index) => (
          <CarouselDot
            key={index}
            index={index}
            activeIndex={activeIndex}
            setActiveIndex={setActiveIndex}
          />
        ))}
      </Flex>
    </Box>
  );
};

Carousel.propTypes = {
  activeIndex: PropTypes.number.isRequired,
  setActiveIndex: PropTypes.func.isRequired,
  carouselWidth: PropTypes.number.isRequired,
  setCarouselWidth: PropTypes.func.isRequired,
  advanceSlider: PropTypes.func.isRequired,
  slides: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      author: PropTypes.shape({
        name: PropTypes.string,
        role: PropTypes.string,
      }),
      beforeImage: PropTypes.string,
      bgImage: PropTypes.string,
      maxHeight: PropTypes.number,
      minHeight: PropTypes.number,
      subtitle: PropTypes.string,
      text: PropTypes.string,
      title: PropTypes.string,
    }),
  ).isRequired,
};

export default Carousel;
