import React from 'react';
import { Image } from 'rebass';
import PropTypes from 'prop-types';
import LivePersonLogo from '../assets/logo.svg';

const Logo = ({ smallSize, isStatic }) => {
  const getDisplay = () => {
    if (isStatic) {
      return 'block';
    }
    if (smallSize) {
      return ['block', 'block', 'none'];
    }
    return ['none', 'none', 'block'];
  };

  return (
    <Image
      src={LivePersonLogo}
      alt="LivePerson logo"
      px={smallSize ? [0, 0, 2] : 4}
      pt={smallSize ? 3 : 6}
      pb={3}
      sx={{
        display: getDisplay(),
        position: isStatic ? 'static' : ['static', 'static', 'absolute'],
        top: 0,
        left: 0,
        zIndex: 2,
        width: smallSize ? 170 : 215,
        height: smallSize ? 66 : 83,
      }}
    />
  );
};

Logo.defaultProps = {
  smallSize: false,
  isStatic: false,
};

Logo.propTypes = {
  smallSize: PropTypes.bool,
  isStatic: PropTypes.bool,
};

export default Logo;
