import React from 'react';
// TODO: the value for these countries should be the two-letter short code
import countries from './assets/countries.json';

const emailPattern = /^[A-Za-z0-9._%-]+@[A-Za-z0-9.-]+\.[a-zA-Z]{2,4}$/;

export const validateEmail = (email) => emailPattern.test(email);

export const convertTextToLines = (text) => {
  if (typeof text === 'string' && text.indexOf('\n') > -1) {
    return text.split('\n').map((t, i) => <div key={i}>{t}</div>);
  }
  return text;
};

const parseCookie = (cookie, name) => {
  const match = cookie.match(new RegExp(`(^| )${name}=([^;]+)`));
  return match && match[2].toLowerCase() !== 'null' ? match[2] : undefined;
};

export const getCountries = () => [
  { name: 'Select country', value: 'CHOOSE_COUNTRY', disabled: true },
  ...countries.sort((a, b) => (a.name < b.name ? -1 : 1)),
].filter((country) => !country.isBanned);

// This is used to send cookie information about the user to Salesforce
// As of Oct 2020, this data is still being used
// Questions surrounding the usage of this data can be directed to the Marketing Team
export const getMarketingData = (cookie) => ({
  marketingData: {
    leadSource: parseCookie(cookie, 'lp-leadSource') || '',
    referringUrl: parseCookie(cookie, 'lp-lsRef') || '',
    utmCampaignId: parseCookie(cookie, 'lp-lsCampaign') || '',
    utmCampaignMedium: parseCookie(cookie, 'lp-lsMedium') || '',
    utmCampaignSearchKeywords: parseCookie(cookie, 'lp-lsTerms') || '',
    utmCampaignSource: parseCookie(cookie, 'lp-lsSource') || '',
    utmContent: parseCookie(cookie, 'lp-lsContent') || '',
  },
});
