import React, { useState } from 'react';
import { Box, Text, Link } from 'rebass';
import PropTypes from 'prop-types';
import Carousel from './Carousel';
import Logo from './Logo';
import carouselData from '../assets/carouselData.json';
import Header from './Header';
import RegistrationForm from './RegistrationForm';
import Title from './Title';

const Home = ({
  setMessageState, userDetails, setUserDetails,
}) => {
  const [carouselIndex, setCarouselIndex] = useState(0);
  const [carouselWidth, setCarouselWidth] = useState(0);

  const advanceSlider = () => {
    setCarouselIndex((index) => (index + 1 >= carouselData.length ? 0 : index + 1));
  };

  return (
    <Box
      sx={{
        height: '100%',
        position: 'relative',
        display: ['block', 'block', 'grid'],
        gridTemplateColumns: '5fr 2fr 9fr',
        gridTemplateAreas: '\'carousel spacer form\'',
        '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
          display: '-ms-grid',
          msGridColumns: '5fr 2fr 9fr',
        },
      }}
      mx={[5, 6, 0]}
    >
      <Logo />
      <Carousel
        slides={carouselData}
        activeIndex={carouselIndex}
        setActiveIndex={setCarouselIndex}
        carouselWidth={carouselWidth}
        setCarouselWidth={setCarouselWidth}
        advanceSlider={advanceSlider}
      />
      <Box
        sx={{
          gridArea: 'spacer',
          msGridColumn: '2',
          height: '100vh',
          display: ['none', 'none', 'block'],
        }}
      />
      <Box
        sx={{
          flex: 1,
          overflow: 'auto',
          gridArea: 'form',
          msGridColumn: '3',
        }}
      >
        <Header />
        <Title />
        <RegistrationForm
          setMessageState={setMessageState}
          userDetails={userDetails}
          setUserDetails={setUserDetails}
        />
        <Text variant="privacyPolicy" mt={6} mb={7}>
          By creating an account, I agree to LivePerson&apos;s&nbsp;
          <Link
            sx={{
              textDecoration: 'none',
              color: 'electricBlue',
              '&hover': {
                color: 'electricBlueHover',
              },
            }}
            href="https://www.liveperson.com/policies/license-agreement/"
          >
            End User License Agreement
          </Link>
        </Text>
      </Box>
    </Box>
  );
};

Home.propTypes = {
  setMessageState: PropTypes.func.isRequired,
  userDetails: PropTypes.shape({
    firstName: PropTypes.string,
    lastName: PropTypes.string,
    password: PropTypes.string,
    region: PropTypes.string,
    email: PropTypes.string,
    company: PropTypes.string,
  }).isRequired,
  setUserDetails: PropTypes.func.isRequired,
};

export default Home;
