/* eslint-disable no-unused-vars */
/* eslint-disable camelcase */
import React, { useState, useEffect } from 'react';
import {
  Flex, Box, Image, Text,
} from 'rebass';
import { Button, Input } from '@liveperson/lp-react-components';
import { Link, Redirect, useLocation } from 'react-router-dom';
import Logo from './Logo';
import ResendStatus from './ResendStatus';
import ShieldIcon from '../assets/icon-shield.svg';
import ErrorIcon from '../assets/icon-error.svg';

const LOGO_SIZE = 44;
const INPUT_SIZE = 44;
const INPUTS = [1, 2, 3, 4, 5];
const SERVER_ERROR = 'SERVER_ERROR';
const OTP_EXCEEDED = 'OTP_CREATION_LIMIT_EXCEEDED';
// TOOD: clean up these styles and put them in the theme file where possible
// TODO: refactor this file into smaller components

const gridTemplateColumns = `${INPUT_SIZE}px ${INPUT_SIZE}px ${INPUT_SIZE}px ${INPUT_SIZE}px ${INPUT_SIZE}px`;

// TOOD: import new component library so that I can pass style props to the components
// TOOD: max length of input field - set to 1?

let interval;

function OneTimePasscode() {
  const [digits, setDigits] = useState(['', '', '', '', '']);
  const [errors, setErrors] = useState([false, false, false, false, false]);
  const [errorMessage, setErrorMessage] = useState('');
  const [isValidOTP, setIsValidOTP] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [resendSeconds, setResendSeconds] = useState(null);
  const [selectedDigit, setSelectedDigit] = useState(0);
  const [resendAttempts, setResendAttempts] = useState(0);
  const location = useLocation();
  const creationId = location?.state?.creationId;

  // TODO: memoize these functions so that they don't get recreaed on render
  const startResendTimer = () => {
    clearInterval(interval);
    let seconds = 5;
    setResendSeconds(seconds);
    interval = setInterval(() => {
      if (seconds > 0) {
        seconds -= 1;
      } else {
        seconds = 0;
        clearInterval(interval);
      }
      setResendSeconds(seconds);
    }, 1000);
  };

  const isDigitErrored = (digit) => {
    const number = parseInt(digit, 10);
    return !(number >= 0 && number <= 9);
  };

  const validateDigits = () => {
    setErrorMessage('');
    const digitErrors = digits.map(isDigitErrored);
    setErrors(digitErrors);
    return digitErrors.reduce((a, d) => !d && a, true);
  };

  const resetErrors = () => {
    setErrors([false, false, false, false, false]);
    setErrorMessage('');
  };

  const updateError = (e) => {
    switch (e.message) {
      case 'OTP_EXPIRED':
        setErrorMessage('The current OTP code has expired, please click the Resend link to try again');
        break;
      case 'OTP_VERIFICATION_FAILED':
        setErrorMessage('Invalid OTP, please try again');
        break;
      case 'OTP_VERIFICATION_LIMIT_EXCEEDED':
        setErrorMessage('You have exceeded your attempts, please try resending OTP');
        break;
      case OTP_EXCEEDED:
        setErrorMessage(OTP_EXCEEDED);
        break;
      default:
        setErrorMessage(SERVER_ERROR);
        break;
    }
  };

  const verifyOTP = async () => {
    if (validateDigits()) {
      resetErrors();
      setIsLoading(true);
      await fetch(`${process.env.REACT_APP_ENDPOINT}/signup/${creationId}/otp`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Accept: 'application/json',
        },
        body: JSON.stringify({ otp_passcode: parseInt(digits.join(''), 10) }),
      }).then((response) => (response.status === 200 ? response : response.json()))
        .then((result) => {
          const { code, status } = result;
          if (code) {
            throw new Error(code);
          }
          if (status === 200) {
            setIsValidOTP(true);
          }
        }).catch((e) => {
          setErrors([true, true, true, true, true]);
          updateError(e);
        });
    } else {
      setErrorMessage('Code not filled out correctly');
    }
    setIsLoading(false);
  };

  const resendOTP = async () => {
    // Clear the current fields since we are generating a new code
    setDigits(['', '', '', '', '']);
    resetErrors();

    // Set the timer to null, to show a 'sent message' state
    setResendSeconds(null);

    // Start the resent timer after 5 seconds
    setTimeout(startResendTimer, 10000);

    await fetch(`${process.env.REACT_APP_ENDPOINT}/signup/${creationId}/otp`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json',
      },
    }).then((response) => response.json())
      .then((result) => {
        if (typeof result === 'object') {
          const { code, otpResendAttempts } = result;
          if (otpResendAttempts) {
            setResendAttempts(otpResendAttempts);
          } else if (code) {
            throw new Error(code);
          } else {
            throw new Error(SERVER_ERROR);
          }
        } else {
          throw new Error(SERVER_ERROR);
        }
      }).catch(updateError);
  };

  const handleNumberChange = (name, value, index) => {
    setSelectedDigit(index);
    resetErrors();
    const newDigits = [...digits];
    newDigits[index] = value;
    setDigits(newDigits);
  };

  const getTitle = () => {
    switch (errorMessage) {
      case OTP_EXCEEDED:
        return 'Sorry. You have maxed your attempt limits';
      case SERVER_ERROR:
        return 'Seems something is not working';
      default:
        return 'Let\'s verify your account';
    }
  };

  const getDescription = () => {
    switch (errorMessage) {
      case OTP_EXCEEDED:
        return 'Some lorem ipsum text that goes here. This will talk abou them trying to Sign up again or come back later to trying aigning up for an account';
      case SERVER_ERROR:
        return 'Dont worry. We have your data saved and you would not need to refill everything. Please verify your details again and try signing up again for your free trial account';
      default:
        return 'Please verify your account by entering the One Time Passcode (OTP) sent to your email address';
    }
  };

  useEffect(() => {
    setTimeout(startResendTimer, 5000);
    return () => setIsValidOTP(false);
  }, []);

  if (isValidOTP) {
    return (
      <Redirect
        push
        to={{
          pathname: `${process.env.PUBLIC_URL}/creating-account`,
          state: { digits, creationId },
        }}
      />
    );
  }

  const isErrored = (errorMessage === OTP_EXCEEDED) || (errorMessage === SERVER_ERROR);

  return (
    <Box
      backgroundColor="navy"
      sx={{
        height: '100%',
        flex: 1,
        overflow: 'auto',
        display: 'grid',
        color: 'white',
        gridTemplateRows: 'auto auto 1fr',
        gridTemplateAreas: "'logo' 'fields' 'line'",
        '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
          display: '-ms-grid',
          msGridRows: 'auto auto 1fr',
        },
      }}
    >
      <Box sx={{ gridArea: 'logo', msGridRow: '1' }} mb={3}>
        <Link to={`${process.env.PUBLIC_URL}/`}>
          <Logo isStatic />
        </Link>
      </Box>
      <Flex
        alignItems="center"
        flexDirection="column"
        sx={{ gridArea: 'fields', msGridRow: '1' }}
      >
        <Flex
          justifyContent="center"
          alignItems="center"
          flexDirection="column"
          maxWidth={isErrored ? 700 : 'auto'}
        >
          <Image
            src={isErrored ? ErrorIcon : ShieldIcon}
            alt="shield"
            height={LOGO_SIZE}
            width={LOGO_SIZE}
          />
          <Text
            sx={{
              gridArea: 'fields',
              msGridRow: '1',
              fontSize: 4,
              fontWeight: 700,
              lineHeight: '32px',
              letterSpacing: '0.008em',
              WebkitFontSmoothing: 'antialiased',
              textAlign: 'center',
            }}
            my={3}
            px={4}
          >
            {getTitle()}
          </Text>
          <Text
            sx={{
              gridArea: 'fields',
              msGridRow: '1',
              fontSize: 2,
              lineHeight: '24px',
              letterSpacing: '0.01em',
              WebkitFontSmoothing: 'antialiased',
              textAlign: 'center',
            }}
            mb={6}
            px={4}
          >
            {getDescription()}
          </Text>
          {!isErrored && (
            <Box mb={5}>
              <Flex
                flexDirection={['column', 'row']}
                alignItems="center"
              >
                <form
                  autoComplete="off"
                  style={{
                    display: 'grid',
                    gridGap: 12,
                    gridTemplateColumns,
                    marginBottom: [3, 0],
                  }}
                >
                  {INPUTS.map((id, index) => (
                    <Input
                      key={id}
                      isFullWidth
                      type="number"
                      height={INPUT_SIZE}
                      fontSize={2}
                      maxLength={1}
                      name={`input-${id}`}
                      value={digits[index]}
                      isErrored={errors[index]}
                      shouldFocusOnLoad={selectedDigit === index}
                      handleFocus={() => {
                        // TODO: select all the text in the current input
                        // - This will require access to the focus event from the component library
                        setSelectedDigit(index);
                      }}
                      handleChange={(name, value) => {
                        handleNumberChange(name, value, index);
                        if (value !== '') { // Don't select the next digit if the field is empty
                          setSelectedDigit((d) => d + 1);
                        }
                      }}
                      onKeyUp={(e) => (e.key === 'Enter') && verifyOTP()}
                    />
                  ))}
                </form>
                <Button
                  label="Verify OTP"
                  px={5}
                  ml={[0, 6]}
                  mt={[3, 0]}
                  fontSize={1}
                  sx={{
                    // TODO: these states should be in the button theme
                    borderRadius: 20,
                    height: 40,
                    cursor: errorMessage !== '' ? 'default' : 'pointer',
                    opacity: errorMessage !== '' ? 0.3 : 1,
                    '&:hover': {
                      backgroundColor: errorMessage !== '' ? 'electricBlue' : 'electricBlueHover',
                    },
                    position: 'relative',
                  }}
                  onClick={verifyOTP}
                  isLoading={isLoading}
                  disabled={errorMessage !== ''}
                />
              </Flex>
              <Text sx={{
                color: '#F29295',
                fontSize: 0,
                lineHeight: '20px',
                letterSpacing: '0.01em',
                WebkitFontSmoothing: 'antialiased',
                alignSelf: ['none', 'flex-start'],
                marginTop: 3,
                opacity: errorMessage ? 1 : 0,
              }}
              >
                {(!isLoading && errorMessage) || 'no error'}
              </Text>
            </Box>
          )}
        </Flex>
      </Flex>
      {!isErrored && (
        <Flex sx={{ gridArea: 'line' }} px={5} justifyContent="center">
          <Flex
            justifyContent={['initial', 'space-between']}
            flexDirection={['column', 'row']}
            alignItems={['center', 'flex-start']}
            sx={{ borderTop: '1px solid #6C7189' }}
            maxWidth={1020}
            flex={1}
            pb={5}
          >
            <Flex fontSize={0} mt={6} lineHeight="20px" sx={{ WebkitFontSmoothing: 'antialiased' }}>
              <Text fontWeight={600} mr={1} flexShrink={0}>Tip:</Text>
              Don&apos;t see the email in your inbox? Check your spam folder
            </Flex>
            <ResendStatus
              resendOTP={resendOTP}
              resendSeconds={resendSeconds}
              attempts={resendAttempts}
            />
          </Flex>
        </Flex>
      )}
      {isErrored && (
        <Flex justifyContent="center">
          <Link to={`${process.env.PUBLIC_URL}/`} style={{ textDecoration: 'none' }}>
            <Button
              label="Sign up again"
              fontSize={1}
              px={5}
              sx={{
                borderRadius: 20,
                height: 40,
                cursor: 'pointer',
                '&:hover': {
                  backgroundColor: 'electricBlueHover',
                },
              }}
            />
          </Link>

        </Flex>
      )}
    </Box>
  );
}

export default OneTimePasscode;
