/* eslint-disable no-unused-vars */
import React from 'react';
import {
  Box,
  Flex, Image, Text,
} from 'rebass';
import PropTypes from 'prop-types';
import { LoadingDots } from '@liveperson/lp-react-components';
import Check from '../assets/icon-check.svg';

const ICON_SIZE = 18;

function ResendStatus({ resendOTP, resendSeconds, attempts }) {
  let children;

  switch (resendSeconds) {
    case null:
      children = (
        <>
          <Image
            src={Check}
            alt="check"
            height={ICON_SIZE}
            width={ICON_SIZE}
            mr={3}
          />
          OTP sent to your email address
        </>
      );
      break;
    case 0:
      children = (
        <Text
          fontSize={0}
          sx={{
            WebkitFontSmoothing: 'antialiased',
            color: 'aqua',
            cursor: 'pointer',
            fontWeight: 700,
            '&:hover': {
              textDecoration: 'underline',
            },
          }}
          onClick={resendOTP}
        >
          {`Resend OTP ${(attempts > 0) ? `(attempt: ${attempts} of 3)` : ''}`}
        </Text>
      );
      break;
    default:
      children = (
        <>
          <Text mr={1}>{`Resend OTP in ${resendSeconds} seconds`}</Text>
          <Box sx={{ transform: 'scale(0.8) translateY(4px)' }}>
            <LoadingDots />
          </Box>
          {/* TODO: make these loading dots smaller by providing a size option */}
        </>
      );
      break;
  }

  return (
    <Flex
      alignItems="center"
      fontSize={0}
      mt={6}
      sx={{
        WebkitFontSmoothing: 'antialiased',
        color: 'white',
        lineHeight: '20px',
      }}
    >
      {children}
    </Flex>
  );
}

ResendStatus.defaultProps = {
  resendSeconds: null,
};

ResendStatus.propTypes = {
  resendOTP: PropTypes.func.isRequired,
  resendSeconds: PropTypes.number,
  attempts: PropTypes.number.isRequired,
};

export default ResendStatus;
