/* eslint-disable camelcase */
/* global hj */

import React, { useState } from 'react';
import { Box } from 'rebass';
import {
  InputField, SelectField, Button,
} from '@liveperson/lp-react-components';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
import Recaptcha from './Recaptcha';
import {
  validateEmail, getCountries, getMarketingData,
} from '../utils';

// This data is being used by the marketing team to track users using cookies
const MARKETING_DATA = getMarketingData(document.cookie);

const INTITIAL_COUNTRY = 'CHOOSE_COUNTRY';
const DUPLICATE_EMAIL = 'DUPLICATE_EMAIL';
const UNKNOWN_ERROR = 'UNKNOWN_ERROR';

// This provides the initial error state, where there are no errors, but the messages are defined
const getErrorData = () => ({
  firstName: {
    message: 'Required field',
  },
  lastName: {
    message: 'Required Field',
  },
  company: {
    message: 'Required Field',
  },
  email: {
    message: 'The business email address entered is not valid.',
  },
  emailDuplicate: {
    message: 'This email has already been used to create a free trial account. Please use a different email to sign up or sign in to your current account ',
    isError: true,
    hasInteracted: true,
  },
  password: {
    message: 'Your password does not meet all the requirements',
    requirements: {
      name: 'Your password must consist of:',
      data: [
        { name: '- minimum of 8 characters' },
        { name: '- one or more uppercase letters (A-Z)' },
        { name: '- one or more lowercase letters (a-z)' },
        { name: '- one or more numbers (0-9)' },
        { name: '- one or more of these special characters:' },
        { name: '& @ ! * $ < > / ( ) % # =', shouldIndent: true },
        { name: '- cannot use any other special characters' },
      ],
    },
  },
  region: {
    message: 'Required Field',
  },
  recaptchaResponseToken: {
    message: 'Required Field',
  },
});

const RegistrationForm = ({
  setMessageState, userDetails, setUserDetails,
}) => {
  const [errors, setErrors] = useState(getErrorData());
  const [isDuplicateEmail, setIsDuplicateEmail] = useState(false);
  const [creationId, setCreationId] = useState(false);

  const parseZoneFromRegion = () => getCountries().find(
    (c) => c.value === userDetails.region,
  )?.region;

  const updateError = (name, obj) => {
    setErrors((prevErrors) => ({
      ...prevErrors,
      [name]: {
        ...prevErrors[name],
        ...obj,
      },
    }));
  };

  const updateRequirement = (index, value) => {
    setErrors((prevErrors) => {
      const newErrors = { ...prevErrors };
      newErrors.password.requirements.data[index].isError = value;
      return newErrors;
    });
  };

  const isPasswordErrored = (value) => {
    // Check length
    const hasLength = value.length >= 8;
    updateRequirement(0, !hasLength);

    // Check uppercase
    const hasUppercase = value.match(/[A-Z]+/) !== null;
    updateRequirement(1, !hasUppercase);

    // Check lowercase
    const hasLowercase = value.match(/[a-z]+/) !== null;
    updateRequirement(2, !hasLowercase);

    // Check numbers
    const hasNumber = value.match(/[0-9]+/) !== null;
    updateRequirement(3, !hasNumber);

    // Check special characters
    const hasSpecial = value.match(/[&@!*$<>/()%#=]+/) !== null;
    updateRequirement(4, !hasSpecial);
    updateRequirement(5, !hasSpecial);

    // Check special characters
    const hasNoExtra = value.match(/^[(0-9)(a-z)(A-Z)&@!*$<>/()%#=]+$/) !== null;
    updateRequirement(6, !hasNoExtra);

    return !hasLength || !hasUppercase || !hasLowercase || !hasNumber || !hasSpecial || !hasNoExtra;
  };

  const validateUserDetail = (name, userDetail) => {
    let isError;

    switch (name) {
      case 'email':
        // There is an error if it is invalid, or if it's a duplicate email
        isError = isDuplicateEmail || !validateEmail(userDetail);
        break;
      case 'password':
        isError = isPasswordErrored(userDetail);
        break;
      case 'firstName':
      case 'lastName':
      case 'company':
        isError = userDetail.length < 1;
        break;
      case 'region':
        isError = userDetail === INTITIAL_COUNTRY;
        break;
      default:
        isError = userDetail === '';
    }

    updateError(name, {
      hasInteracted: true,
      isError,
    });

    return isError;
  };

  const updateUserDetail = (name, userDetail) => {
    // If the email is changing, we should reset the duplicate email error
    if (name === 'email') {
      setIsDuplicateEmail(false);
      updateError(name, {
        hasInteracted: true,
        isError: false,
      });
    }
    setUserDetails({ ...userDetails, [name]: userDetail });
    validateUserDetail(name, userDetail);
  };

  const hasErrors = () => {
    const userDetailErrors = Object.keys(userDetails).map(
      (item) => validateUserDetail(item, userDetails[item]),
    );
    return userDetailErrors.reduce((item, acc) => acc || item, false);
  };

  const signUpForAccount = () => {
    fetch(`${process.env.REACT_APP_ENDPOINT}/signup`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json',
      },
      body: JSON.stringify({
        ...userDetails,
        region: parseZoneFromRegion(), // parse region into zone
        ...MARKETING_DATA,
      }),
    })
      .then((response) => {
        if (response.status === 409) {
          throw new Error(DUPLICATE_EMAIL);
        }
        return response.json();
      })
      .then(({ code, description, accountCreationId }) => {
        if (accountCreationId) {
          setCreationId(accountCreationId);
        } else if (description) {
          throw new Error(description);
        } else if (code) {
          throw new Error(code);
        } else {
          throw new Error(UNKNOWN_ERROR);
        }
      })
      .catch((e) => {
        if (e.message === DUPLICATE_EMAIL) {
          // Account has already been created with this email
          setIsDuplicateEmail(true);
          updateError('email', { isError: true });
        } else {
          // NOTE: Why are we setting the error state here? What is the use case?
          setMessageState('error');
        }
        // Reset recaptcha after error
        window?.grecaptcha?.reset();
      });
  };

  const createAccount = (event) => {
    event.preventDefault();
    const hotJarIsEnabled = typeof hj === 'function';

    if (!hasErrors()) {
      signUpForAccount();
      if (hotJarIsEnabled) {
        hj('formSubmitSuccessful');
      }
    } else if (hotJarIsEnabled) {
      hj('formSubmitFailed');
    }
  };

  if (creationId) {
    return (
      <Redirect
        push
        to={{
          pathname: `${process.env.PUBLIC_URL}/enter-passcode`,
          state: { creationId },
        }}
      />
    );
  }

  return (
    <Box sx={{ position: 'relative' }}>
      <form data-testid="registerForm" autoComplete="off">
        <Box sx={{
          display: 'grid',
          gridTemplateColumns: ['1fr 1fr', '2.5fr 2.5fr 4fr'],
          gridTemplateAreas: '\'firstname lastname\'',
          gridGap: 3,
          marginBottom: 3,
          '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
            display: '-ms-grid',
            msGridColumns: '2.5fr 2.5fr 4fr',
          },
        }}
        >
          <Box sx={{
            gridArea: 'firstname',
            msGridColumn: '1',
            '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
              marginRight: 2,
            },
          }}
          >
            <InputField
              name="firstName"
              type="text"
              value={userDetails.firstName}
              handleChange={updateUserDetail}
              handleBlur={updateError}
              placeholder="First name"
              error={errors.firstName}
              shouldFocusOnLoad
              isFullWidth
            />
          </Box>
          <Box sx={{
            gridArea: 'lastname',
            msGridColumn: '2',
            '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
              marginLeft: 2,
            },
          }}
          >
            <InputField
              name="lastName"
              type="text"
              value={userDetails.lastName}
              handleChange={updateUserDetail}
              handleBlur={updateError}
              placeholder="Last name"
              error={errors.lastName}
              isFullWidth
            />
          </Box>
        </Box>
        <Box mb={3}>
          <InputField
            name="company"
            type="text"
            value={userDetails.company}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            placeholder="Enter business name"
            error={errors.company}
          />
        </Box>
        <Box mb={3}>
          <InputField
            name="email"
            type="email"
            value={userDetails.email}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            placeholder="Enter business email address"
            error={isDuplicateEmail ? errors.emailDuplicate : errors.email}
          />
        </Box>
        <Box mb={3}>
          <InputField
            name="password"
            type="password"
            value={userDetails.password}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            placeholder="Create password"
            error={errors.password}
          />
        </Box>
        <Box mb={3}>
          <SelectField
            name="region"
            value={userDetails.region || 'choose-country'}
            handleChange={updateUserDetail}
            handleBlur={updateError}
            error={errors.region}
            options={getCountries()}
          />
        </Box>
        <Recaptcha
          handleChange={updateUserDetail}
          error={errors.recaptchaResponseToken}
        />
        <Box
          pt={4}
          sx={{
            display: 'grid',
            gridTemplateColumns: ['auto', '5fr 4fr'],
            '@media all and (-ms-high-contrast: none), (-ms-high-contrast: active)': {
              display: '-ms-grid',
              msGridColumns: '5fr 4fr',
            },
          }}
        >
          <Button
            data-testid="registerButton"
            handleClick={createAccount}
            label="Create Account"
            isFullWidth
            variant="primary"
          />
        </Box>
      </form>
    </Box>
  );
};

RegistrationForm.propTypes = {
  setMessageState: PropTypes.func.isRequired,
  setUserDetails: PropTypes.func.isRequired,
  userDetails: PropTypes.shape({
    firstName: PropTypes.string,
    lastName: PropTypes.string,
    password: PropTypes.string,
    region: PropTypes.string,
    email: PropTypes.string,
    company: PropTypes.string,
  }).isRequired,
};

export default RegistrationForm;
