import React from 'react';
import { Box, Text, Image } from 'rebass';
import PropTypes from 'prop-types';
import { convertTextToLines } from '../utils';

const CarouselItem = ({ item, carouselWidth }) => {
  // eslint-disable-next-line
  const backgroundImage = 'url(' + process.env.PUBLIC_URL + /img/ + item.bgImage + ')';

  return (
    <Box
      px={5}
      sx={{
        height: '100%',
        width: carouselWidth,
        backgroundImage,
        backgroundSize: 'cover',
        paddingTop: 104,
        paddingBottom: 84,
        display: item.id === 'conversational-ai' ? 'flex' : 'block',
        alignItems: 'center',
        overflow: 'auto',
      }}
    >
      {item.beforeImage && (
        <Image
          src={`${process.env.PUBLIC_URL}/img/${item.beforeImage}`}
          alt={item.altText}
          px={3}
          py={5}
          sx={{
            margin: '0 auto',
            display: 'block',
            width: item.id === 'channels' ? ['100%', '100%', '90%', '80%', '70%'] : ['100%', '100%', '50%', '40%'],
            height: 'auto',
          }}
        />
      )}
      {item.title && <Text variant="carouselTitle" mt={1}>{convertTextToLines(item.title)}</Text>}
      {item.text && <Text variant="carouselText" mt={1}>{convertTextToLines(item.text)}</Text>}
      {item.subtitle && <Text variant="carouselSubtitle" mt={1}>{convertTextToLines(item.subtitle)}</Text>}
      {item.author && (
        <>
          <Text variant="carouselAuthorName" mt={3}>{convertTextToLines(item.author.name)}</Text>
          <Text variant="carouselAuthorRole" mt={1}>{convertTextToLines(item.author.role)}</Text>
        </>
      )}
    </Box>
  );
};

CarouselItem.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.string,
    author: PropTypes.shape({
      name: PropTypes.string,
      role: PropTypes.string,
    }),
    beforeImage: PropTypes.string,
    bgImage: PropTypes.string,
    maxHeight: PropTypes.number,
    minHeight: PropTypes.number,
    subtitle: PropTypes.string,
    text: PropTypes.string,
    title: PropTypes.string,
    altText: PropTypes.string,
  }).isRequired,
  carouselWidth: PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string,
  ]).isRequired,
};

export default CarouselItem;
