import React from 'react';
import { Box } from 'rebass';
import PropTypes from 'prop-types';

const CarouselDot = ({ index, activeIndex, setActiveIndex }) => (
  <Box
    onClick={(event) => {
      // Prevent click from going through to parent
      event.stopPropagation();
      setActiveIndex(index);
    }}
    data-testid="carousel-dot"
  >
    <Box
      data-testid="carousel-dot-inner"
      m={1}
      sx={{
        backgroundColor: activeIndex === index ? 'orange' : 'transparent',
        borderColor: activeIndex === index ? 'orange' : 'white',
        width: 12,
        height: 12,
        borderRadius: 12,
        cursor: 'pointer',
        borderWidth: 1,
        borderStyle: 'solid',
      }}
    />
  </Box>
);

CarouselDot.propTypes = {
  index: PropTypes.number.isRequired,
  activeIndex: PropTypes.number.isRequired,
  setActiveIndex: PropTypes.func.isRequired,
};

export default CarouselDot;
